
.appWrapper {
 padding: 40px 10px 25px 10px;
}

.comingSoonText {
 text-align: center;
}

span {
 text-align: center;
}

#teaser_text{
  text-align: center;
  max-width: 88rem;
  margin: 0 auto;
  padding: 0 1.5rem;
  font-weight: 700;
  font-size: 2.9rem;
  line-height: 4rem;

  span{
    display: block;
    &.large_text{
      font-size: 3.7rem;
			margin: 2rem -0.5rem;
    }
  }
}

#timer_wrapper{
	>span{
		display: inline-block;
    	position: relative;
    	padding: 0 6rem;
    	font-size: 3.2rem;
    	&:after{
    		position:absolute;
    		content: '';
    		width: 0.3rem;
    		height: 100%;
    		top: 0;
    		right: 0;

			/* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#ffffff+0,ffffff+50,ffffff+100&0+1,1+50,0+100 */ /* FF3.6-15 */
			background: -webkit-linear-gradient(top, rgba(255,255,255,0) 0%,rgba(255,255,255,0) 1%,rgba(255,255,255,1) 50%,rgba(255,255,255,0) 100%); /* Chrome10-25,Safari5.1-6 */
			background: -webkit-gradient(linear, left top, left bottom, from(rgba(255,255,255,0)),color-stop(1%, rgba(255,255,255,0)),color-stop(50%, rgba(255,255,255,1)),to(rgba(255,255,255,0)));
			background: -o-linear-gradient(top, rgba(255,255,255,0) 0%,rgba(255,255,255,0) 1%,rgba(255,255,255,1) 50%,rgba(255,255,255,0) 100%);
			background: linear-gradient(to bottom, rgba(255,255,255,0) 0%,rgba(255,255,255,0) 1%,rgba(255,255,255,1) 50%,rgba(255,255,255,0) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
			filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00ffffff', endColorstr='#00ffffff',GradientType=0 ); /* IE6-9 */

    	}

    	&:last-child:after{
    		display: none;
    	}
	}

	.dynamic_value{
		font-size: 12.1rem;
	    display: block;
    	line-height: 1;

    	&+span{
    		position: relative;
    		top: 2rem;
    	}
	}
}

// responsive
// tablet
@media screen and (max-width: 1020px) and (min-width: 741px){
	#timer_wrapper{
		> span{
		    padding: 0 3rem;
    		font-size: 2.2rem;
		}

		.dynamic_value{
			font-size: 9.1rem;
		}
	}
}

body{
// mobile
@media only screen and (max-width: 740px) {
	#timer_wrapper{
		>span{
			padding: 0 1.3rem;
		    font-size:1.5rem;
		}

		.dynamic_value{
			font-size: 4.1rem;

			&+span{
				top: 0.5rem;
			}
		}
	}

	.centreText{
		margin-top: 2rem;
	}

 }


  // medium
  @media only screen and (max-width: 640px) {
    .appWrapper  {
      padding-top:0;
			margin-top: 2rem;
    }

    #teaser_text{
			font-size: 1.7rem;
			line-height: 2.7rem;
			font-weight: 400;

      .large_text{
        font-size: 1.9rem;
      }
    }

		.draw_content_container{
			padding-top:0;
			.main_form{
				margin-top: 0;
				padding: 3rem;
			}
		}
   }

 }



